import React from "react";
import {Col, Row, Checkbox, Card } from "antd";
// import api from '../../../util/ApiCors'
import api from 'util/ApiCors';
//import { Modal, Button } from 'antd';
import Auxiliary from "util/Auxiliary";
import NextOrders from "components/dashboard/Crypto/NextOrders";
import MaquinaDetail from "components/dashboard/MaquinaDetail";
import {columns} from "routes/dashboard/V3/data_listing";
//import api from 'util/Api';

var _ = require('lodash');

class DashboardV3 extends React.Component {

  constructor(props) {
    super(props)

    this.state = {
      page: false,
      visible:true,
      listOfDashboards: [],
      listofMaquinasOfOrderProd: [],
      dashboard: {
          name: '',
          check: false
      },
      machinesExecution: [],
      machinesFreedom: [],
      checkeds: [],
      showMaquinas: [],
      timerOrders: null
    }
    
    //Timeout para buscar as ordens a cada 5 segndos
    let timerOrders = setInterval(() => {
       
        // console.log('vai buscar as ordens');

        this.getMaquinasInOrderProd();
        
    }, 15000);
    this.setState({timerOrders});

  }
  componentWillMount() {
    this.getDashboards();
    this.getMaquinasInOrderProd();
  }

  componentDidMount() {
    // setInterval(this.updateWindow.bind(this), 6000);
  }


  updateWindow = () => {
    this.setState({
      page: !this.state.page,
    });
    // window.location.reload();
  }

  getDashboards = () => {
    api.get(`Dashboards`, {
    })
    .then((result) => {
        let dataDash = [];
        dataDash = result.data;

        // console.log(result);

        this.setState({
          listOfDashboards: dataDash
        });

    },)
    .catch(function(error) { 
        console.log(error);
    })
  }
  getMaquinasInOrderProd = () => {
      // console.log('buscando ordens');
        api.get(`orderprodMaquina/?limit=999`)
        .then((result) => {
            let dataDash = [];
            dataDash = result.data;
            // console.log(result);
            let filtered = _.filter(dataDash, (o) => {
                if(o.orderProd !== null) {
                  return o.orderProd.status  === "liberada" || o.orderProd.status  === "execução"
                }
            });

            // console.log('filtered',filtered);
            // console.log('liberada',filtered.filter(item => item.statusEtapa === 'liberada'));

            // console.log('execucao',filtered.filter(item => item.orderProd.status === 'execução' && item.statusEtapa === 'execução'));
            
            let execucao = [];
            execucao = filtered.filter(item => item.orderProd.status === 'execução' && item.statusEtapa === 'execução')

            // execucao.forEach(item => {
            //   item.tempo = setInterval(() => {
            //     const tempoRodando = moment(moment().diff(inicio)).utc().format("HH:mm:ss");
            //     let t = tempoRodando;
            //     // console.log(t);
            //     this.setState({tempoRodando: t});
            //   }, 1000);;
            // });

            // console.log(execucao);

            this.setState({
                listofMaquinasOfOrderProd: filtered,
                machinesExecution: execucao,
                machinesFreedom: filtered.filter(item => item.statusEtapa === 'liberada'),
            });
            
            this.forceUpdate();
        },)
        .catch(function(error) { 
            console.log(error);
        });
    
  }

  onChange = (e) =>  {
        // console.log(this.state.listOfDashboards);
        let dash = this.state.checkeds;

        if(e.target.checked) {
            dash.push(e.target.name);
        }else {
            // console.log('vai remover');
            dash = dash.filter(item => item !== e.target.name);
            // console.log(dash2);
        }
        // console.log(dash);

        this.setState({checkeds: dash});
        let showMaq = [];
        let listofDash = this.state.listOfDashboards;
        // eslint-disable-next-line
        dash.forEach(function(item) {
            let maqs = [];
            // eslint-disable-next-line
            listofDash.forEach(item2 => {
                if(item2.cod === item) {
                    item2.machines.forEach(machine => {
                        maqs.push(machine.cod);
                    })
                }
            })
            // eslint-disable-next-line
            maqs.forEach(maq => {
                if(!showMaq.find(item => item === maq)) {
                    showMaq.push(maq);
                }
            });
        });

        // uniqueArray = a.filter(function(item, pos) {
        //     return a.indexOf(item) == pos;
        // })

        this.setState({showMaquinas: showMaq});
        
        // console.log(this.state.checkeds);

        // let newCateg = this.state.dashboard;
        // newCateg.name = e.target.name;
        // newCateg.check = e.target.checked;

        // this.setState({
        //     dashboard: newCateg
        // });


        // this.onChangeDash();
  }

  onChangeDash = async () => {
    let maquinas = [];

    const {listOfDashboards,dashboard, listofMaquinasOfOrderProd} = this.state;

    for (const dash of listOfDashboards) {
      if(dash.cod === dashboard.name){
        for (const machinesInDashboard of dash.machines) {
          for (const machinesInorder of listofMaquinasOfOrderProd) {
            if(machinesInDashboard.cod === machinesInorder.maquina || machinesInDashboard.cod === machinesInorder.montagem){
              maquinas.push(machinesInorder);
            }
          }
        }
      }
    }

   let filteredExec;
   filteredExec = await _.filter(maquinas, (o) => {
     if(o.orderProd.status === "execução"){
    //   console.log(o);
     }
     return o.orderProd.status  === "execução" && o.statusEtapa === "execução"
   })

   let filteredFredom;

   filteredFredom = _.filter(maquinas, (o) => {
     return o.statusEtapa === "liberada"
   })

   //filteredExec = _.uniqBy(filteredExec, 'maquina')

   this.setState({
     machinesExecution: filteredExec,
     machinesFreedom: filteredFredom
   })
  
  }

  handleOk = e => {
    // console.log(e);
    this.setState({
      visible: false,
    });
  };

  render () {
    const {listOfDashboards,dashboard, listofMaquinasOfOrderProd, machinesFreedom} = this.state;
    // let machinesExecution = this.state.machinesExecution;

    // machinesExecution = machinesExecution.slice(0,4);

    let nextOrders = [];
    machinesFreedom.forEach((item) => {
        if(this.state.showMaquinas.find(maq => maq === item.maquina || maq === item.montagem)) {
            nextOrders.push(item);
        }
    });

    
    // let machinesExecFirstCouple = machinesExecution.slice(0,2);
    // let machinesExecSecondCouple = machinesExecution.slice(2,4);
    // let showMaqs = this.state.showMaquinas;
    return  (
      <>
          <Auxiliary>
          <Row>
                <Col span={24}>
                    <Card type="inner" title="Dashboards">
                        <Row>
                            {
                            listOfDashboards.map(dash => {
                                return(
                                <Col span={4}>
                                    <div className="gx-form-row0">
                                    <Checkbox onChange={this.onChange} value={dashboard.check} name={dash.cod}>{dash.cod}</Checkbox>
                                    </div>
                                </Col>
                                )
                            })
                            }
                        </Row>
                    </Card>
                </Col>
            </Row>
            <Row>
              <Col span={13}>
                <Row>
                  {this.state.machinesExecution.map(mach => {
                    
                    let apontamento = mach.orderProd.apontamentos.find(ap => ap.dataFim === "");
                    if(apontamento === undefined){
                      return (<div></div>)
                    }
                    let qtdeApontada = 0;
                    
                    mach.orderProd.apontamentos.forEach(ap => {
                      if(ap.maquina === mach.maquina) {
                          qtdeApontada += ap.qtdeApontada;
                      }
                    });
                  return (
                      // <div style={{display: this.state.showMaquinas.find(item => item === mach.maquina || item === mach.montagem) ? '' :'none'}}>
                      <div style={{width:'50%',display: this.state.showMaquinas.find(item => item === mach.maquina || item === mach.montagem) ? '' :'none'}}>
                          <Col span={24} >
                              <MaquinaDetail
                                  key={mach.orderProd.id}
                                  idOrderProdMaquina={mach.orderProd.id}
                                  apontamentos={mach.orderProd.apontamentos}
                                  idOrderProd={mach.orderProd.id}
                                  title={`${mach.maquina === "" ? mach.montagem : mach.maquina }`}
                                  status={`${mach.orderProd.status === "execução" ? "Em Execução" : mach.orderProd.status }`}
                                  orderProd={ `${mach.orderProd.orderProduction}`}
                                  bgColor={"primary"}
                                  order={mach.orderProd}
                                  cro={`${mach.orderProd.product.cod}`}
                                  pedidoFox={`${mach.orderProd.orderFox}`}
                                  qtde={`${qtdeApontada}`}
                                  programador={`${mach.programador}`}
                                  operador={`${mach.operador}`}
                                  apontamento={apontamento}
                                  dataInicio={apontamento.dataInicio}
                                  show={this.state.showMaquinas.find(item => item === mach.maquina || item === mach.montagem) ? true : false}
                              />
                          </Col>
                      </div>
                      )
                  })}
                </Row>
              </Col>
              <Col span={11}>
                  {
                  listofMaquinasOfOrderProd.length === 0 ? 
                  "":
                  <NextOrders 
                      data={nextOrders}  
                      itemProperties={{ 
                          title:"Proximas ordens",
                          // subtitle: "ordens",
                          data: nextOrders,
                          columns:columns
                      }}
                  />                        
                  }
              </Col>
            </Row>
        </Auxiliary>
      </>
  
    );
  }
};

export default DashboardV3;
